export default {
    props: {
        ticketList: {
            type: Array,
            default() {
                return []
            }
        },
        showTicketList: {
            type: Boolean,
            default: false
        }
    },
    methods: {
        choseTicket (tiketObj) {
            this.$emit('choseTicket', tiketObj)
        }
    },
}
